.menu {
  gap: 1em;
  box-shadow: none;
  height: 40em;
}

.menu__header {
  display: grid;
  grid-template-columns: repeat(6, minmax(0, 1fr));
  grid-template-rows: 100%;

  align-items: flex-end;
  height: 8em;

  button {
    height: 80%;
    border: none;
    padding: 0.5em;
    word-break: break-word;
    hyphens: auto;
    font-size: 1.5rem;
    line-height: 2rem;

    &:after {
      content: "";
      position: absolute;
      width: calc(100% + 2px);
      height: 100%;
      left: 0;
      top: 0;
      border: 2px solid $black;
    }

    &.active {
      height: 100%;
      padding: 0.5em;
    }
  }
}

.menu__body {
  > div {
    > div {
      padding: 1.5em 3em;
    }
  }
}

#dashboard {
  > div {
    p {
      font-size: 2rem;
      font-weight: 600;
      line-height: 3rem;
      color: $black;
    }
  }
}

#orders {
  > div {
    .orders-main {
      gap: 0;

      .orders-main__container {
        &:not(:first-child) {
          .orders-main__header {
            display: none;
          }
        }
      }
      .orders-main__group {
        display: flex;
        flex-direction: column;
      }

      .orders-main__row {
        display: grid;
        grid-template-columns: 0.5fr 1.5fr 1fr 2fr 1fr;
        grid-template-rows: 100%;
        align-items: center;

        padding: 1em 2em;
        gap: 0;

        p {
          text-align: center;
          font-size: 1.125rem !important;
          line-height: 1.5625rem !important;
          &:first-of-type {
            text-align: left;
          }
        }
        .date {
          position: static;
        }
        .actions {
          margin-top: 0;
        }
        div.actions {
          justify-content: flex-start;
          align-items: center;
        }
      }
      .orders-main__header {
        .date {
          display: block;
        }
        padding-block: 1.5em;
      }
    }

    .orders {
      .orders__container {
        gap: 0em;
      }
      .orders__row {
        display: grid;
        grid-template-columns: 1.6fr 3.4fr 1fr;
        grid-template-rows: 100%;

        padding: 1em 2em;
        gap: 0;

        > div {
          flex-direction: column;
        }
      }
      .orders__footer {
        grid-template-columns: 1.6fr 1.6fr 1.8fr 1fr;
        grid-template-rows: 100%;

        margin-top: 1.8em;
      }
      form {
        margin-top: 0;
      }
      .btn {
        margin-top: 0;
        width: fit-content;
      }
    }
  }
}

#addresses {
  > div {
    gap: 0;
    > div {
      flex-direction: row;
      gap: 6em;
    }
  }
}

#payment-methods {
  > div {
    p {
      margin-bottom: 10px;
    }
    .container {
      gap: 0;
      .container__row {
        grid-template-columns: 4fr 3fr 2fr;
        grid-template-rows: 100%;

        row-gap: 0;
        p {
          font-size: 1.125rem;
          &:last-of-type {
            text-align: left;
          }
        }
        a {
          font-size: 1.125rem;
        }
      }

      .container__header {
        grid-template-columns: 4fr 5fr;
        grid-template-rows: 100%;

        p {
          font-size: 2rem;
          &:last-of-type {
            display: inline-block;
          }
        }
      }
    }
    .btn {
      margin-top: 2em;
      width: fit-content;
    }
  }
}

#account-details {
  > div {
    form {
      max-width: 75%;
      gap: 4em;
      > div {
        > div {
          flex-direction: row;
          > * {
            flex-basis: calc(50% - 1em);
          }
        }
      }
      .btn {
        width: fit-content;
      }
    }
  }
}

#logout {
  > div {
    > div {
      flex-direction: row;
      justify-content: center;
      gap: 15%;
      .btn {
        min-width: 25%;
        max-width: 16em;
      }
    }
    p {
      font-size: 3.125em;
    }
  }
}
