@import "variables";

.header-primary {
  h1 {
    margin-block: 0.5em;
  }
  h2 {
    margin-block: 0.5em;
  }
}

.menu {
  display: flex;
  flex-direction: column;
  box-shadow: 0px -3px 5px rgba(0, 0, 0, 0.25);
}

.menu__header {
  display: flex;
  flex-direction: column;

  button {
    position: relative;
    box-sizing: border-box;
    appearance: none;
    background: none;
    border: none;
    font-size: 2rem;
    font-weight: 600;
    line-height: 3rem;
    color: $black;
    padding: 0.5em;
    transition: all ease-in-out 0.12s;
    border: 2px solid $black;
    border-bottom: none;

    &:hover {
      color: $black;
    }

    &.active {
      padding-block: 1em;
    }
  }
}

.menu__body {
  box-sizing: border-box;
  flex-basis: 100%;
  border: 2px solid $black;
  overflow-y: auto;
  min-height: 5em;

  > div {
    display: none;
    height: 100%;

    > div {
      box-sizing: border-box;
      width: 100%;
      min-height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 1.5em;
    }
    &.active {
      display: block;
    }
  }
  &::-webkit-scrollbar {
    width: 1em;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba(255, 87, 178, 0.3);
    border-radius: 30px;
    height: 4em;
  }
}

#dashboard {
  > div {
    min-height: 20em;
    display: flex;
    justify-content: center;
    align-items: center;

    p {
      font-size: 1.125rem;
      line-height: 1.5625rem;
      font-weight: 600;
      color: $black;
      text-align: center;
    }
    a {
      color: $primary-500;
    }
  }
}

#orders {
  > div {
    display: flex;
    flex-direction: column;

    .orders-main {
      display: flex;
      flex-direction: column;
      gap: 2em;

      .orders-main__container {
        display: flex;
        flex-direction: column;
      }

      .orders-main__group {
        height: 100%;
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 100%;
      }

      .orders-main__row {
        position: relative;
        box-sizing: border-box;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0.5em;
        gap: 1em;

        p,
        a {
          text-align: left;
          font-size: 1.125rem;
          line-height: 1.5625rem;
          margin: 0;
        }
        .id {
          color: $primary-500;
        }
        .date {
          color: $primary-300;
          position: absolute;
          right: 0.5em;
          top: 0.5em;
          font-size: 0.75rem;
        }
        .status {
          color: $dark;
        }
        .total {
          color: $dark;
        }
        .actions {
          margin-top: 1em;
        }
        div.actions {
          display: flex;
          gap: 0.5em;
          color: $primary-500;
        }
      }
      .orders-main__header {
        .date {
          display: none;
        }
        p {
          color: $black !important;
          &:first-of-type,
          &:last-of-type {
            text-align: left;
          }
        }
      }
    }

    .orders {
      > p.orders__title {
        font-weight: $fw-7;
        color: $black;
        font-size: 1.5625em;
      }
      > p.orders__subtitle {
        font-size: 1.25em;
        color: $black;
      }

      .orders__container {
        display: flex;
        flex-direction: column;
        gap: 1em;
      }

      .orders__row {
        display: flex;
        flex-direction: column;
        padding: 0.5em;
        gap: 1em;

        p {
          margin: 0;
          font-size: 1.125rem;
          line-height: 1.5625rem;
          &:nth-child(1) {
            color: $black;
          }
          &:nth-child(2) {
            color: $dark;
          }
        }

        > div {
          display: flex;
          gap: 0.5em;

          *:nth-child(1) {
            flex-grow: 1;
            flex-shrink: 0;
            flex-basis: 0;
          }
          *:nth-child(2) {
            flex-grow: 3;
            flex-shrink: 1;
            flex-basis: 0;
          }
        }
      }
      .orders__footer {
        margin-top: 0.5em;
      }
      form {
        margin-top: 1.5em;
      }
      .form-checkbox {
        a {
          font-size: var(--fs-5);
        }
      }
      .btn {
        margin-top: 1em;
        width: 100%;
      }
    }

    .orders-detail,
    .orders-payment {
      display: none;
      &.active {
        display: block;
      }
    }
  }
}

#addresses {
  > div {
    display: flex;
    flex-direction: column;
    gap: 1em;

    > div {
      display: flex;
      flex-direction: column;
      gap: 2em;

      > div {
        display: flex;
        flex-direction: column;
        gap: 0.5em;
      }
    }

    p {
      font-size: 1.125rem;
      line-height: 1.5625rem;
      color: $primary-300;
    }
    a {
      display: inline-flex;
      gap: 0.4em;
      align-items: center;
      font-size: 2rem;
      font-weight: 600;
      i {
        width: 22px;
        height: 22px;
        color: $primary-500;
      }
    }
  }
}

#payment-methods {
  > div {
    p {
      font-size: 1.125rem;
      color: $black;
      margin-bottom: 30px;
    }
    .container {
      display: flex;
      flex-direction: column;
      gap: 1em;
      padding-inline: 0;

      .container__row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        // grid-template-rows: 100%;

        row-gap: 0.5em;
        padding: 0.5em 0;
        p {
          font-size: 1.5rem;
          margin: 0;
          &:last-of-type {
            text-align: right;
          }
        }
        a {
          font-size: 1.5rem;

          color: $primary-500;
        }
      }

      .container__header {
        grid-template-columns: 1fr;
        grid-template-rows: 100%;

        p {
          font-weight: 600;
          color: $black;
          font-size: 2rem;
          &:last-of-type {
            display: none;
          }
        }
      }
    }
    .btn {
      margin-top: 1em;
      font-size: 1.5625rem !important;
      width: 100%;
    }
  }
}

#account-details {
  > div {
    form {
      display: flex;
      flex-direction: column;
      gap: 3em;
      > div {
        display: flex;
        flex-direction: column;
        gap: 1em;
        > div {
          display: flex;
          flex-direction: column;
          gap: 1em;
        }
        p {
          font-size: 1.5625em;
          color: $black;
          font-weight: $fw-7;
          text-transform: uppercase;
          margin: 0;
        }
      }
      .btn {
        width: 100%;
      }
    }
  }
}

#logout {
  > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
    > div {
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 1em;
      .btn {
        width: 100%;
      }
    }
    p {
      font-size: 1.5625em;
      font-weight: $fw-7;
      color: $black;
      text-transform: uppercase;
      text-align: center;
    }
  }
}

@include sm {
  @import "dashboard-sm";
}

@include lg {
  @import "dashboard-lg";
}
